                this.hamburger = false
                // this.title = false
                //뺌
            }
            // console.log(document.querySelector("body").classList.toggle("active"));//.classList.toggle("active");
            // document.querySelector("body").classList.toggle("active")
        },
        menu_var_ff() {
            if (document.querySelector("body").classList.value == "") {
                document.querySelector("body").classList.toggle("active")
                this.hamburger = true
            }
        },
        command_modal(name, ds) {
            // const docu = document.querySelector(`.${name.replace(" ", "")}`)
            // docu.style.display = "block"
            const docu = document.querySelector(".black-bg")
            const docuu = document.querySelector(".commands_title_description_h2")
            const docuuu = document.querySelector(".commands_title_description_span")
            const docuuuu = document.querySelector(".commands_title_description_img")
            docuuuu.src = require(`../../img/example/${name.replace(' ', '')}.gif`)
            //    <!-- <img :src="require('../../img/example/' + command_name[i].replace(' ', '').trim() + '.gif')" alt="ㅎㅇ"> -->
            docuu.innerHTML = name
            docuuu.innerHTML = ds
            docu.style.display = "block"
        },
        command_modal_close() {
            const docu = document.querySelector(".black-bg")
            docu.style.display = "none"
        },
    },
    components: {},
};
</script>

